body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  font-family: 'Open Sans', sans-serif; }

.App {
  text-align: center;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.65)), to(rgba(0, 0, 0, 0.65))), url("https://jinster.github.io/tfm-randomizer/mars.jpg");
  background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), url("https://jinster.github.io/tfm-randomizer/mars.jpg");
  object-fit: fill;
  background-size: cover;
  height: 100vh; }

p {
  color: #FFF; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

.App-button {
  display: flex;
  flex-wrap: wrap;
  margin-top: 10vh;
  justify-content: center;
  align-items: center;
  font-size: 18px; }

.wrapper {
  margin-top: 25px;
  width: 100%; }

.randomize-btn {
  background-color: #4CAF50;
  padding: 25px 42px;
  text-decoration: none;
  color: #FFF;
  cursor: pointer;
  font-size: 16px; }

li {
  margin: 15px; }

.randomize-screen {
  color: #FFF; }

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px; }

/* Hide default HTML checkbox */
.switch input {
  display: none; }

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px; }

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%; }

input:checked + .slider {
  background-color: #2196F3; }

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3; }

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px); }

.award-milestone, .award-milestone-secondary {
  box-sizing: border-box;
  display: flex;
  border: 1px white solid;
  width: 33vw;
  height: 18vh;
  margin-bottom: 5px;
  justify-content: center;
  align-items: center;
  -webkit-backface-visibility: visible;
          backface-visibility: visible;
  -webkit-transform: translate3d(0, 0, 1px);
          transform: translate3d(0, 0, 1px); }
  .award-milestone h3, .award-milestone-secondary h3 {
    font-size: 14px;
    color: white;
    text-shadow: 1px 0 0 #000, 0 -1px 0 #000, 0 1px 0 #000, -1px 0 0 #000; }

.award-milestone-secondary {
  display: block;
  color: white;
  background: #000; }

.am-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.description {
  display: block;
  font-size: 14px;
  padding: 0 2px; }

.title {
  padding: 5px 0;
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 13px;
  background-color: #000;
  font-weight: 800; }

@media (min-width: 360px) {
  .award-milestone, .award-milestone-secondary {
    margin-bottom: 10px; }
    .award-milestone h3, .award-milestone-secondary h3 {
      font-size: 18px; }
  .title {
    font-size: 18px; }
  .description {
    font-size: 16px;
    line-height: 16px; } }

